   }

   .nav {
      //border: 1px solid;
      display: flex;
      justify-content: space-between;
      ul {
         display: flex;
         padding: 0;
         li {
            list-style: none;
            width: 125px;
            a {
               @include Link
            }
         }
      }

   }
}


